import './GraphiQLPanel.scss'

function GraphiQLPanel({
  body,
  className = '',
  footer,
  onTogglePanel,
  title = 'Panel',
}) {
  return (
    <div
      className={`graphiqlpanel-wrap ${
        footer ? 'has-footer' : ''
      } ${className}`}
    >
      <div className="graphiqlpanel-title-bar">
        <div className="graphiqlpanel-title">{title}</div>
        <div className="graphiqlpanel-rhs">
          <div className="graphiqlpanel-close" onClick={onTogglePanel}>
            
          </div>
        </div>
      </div>
      <div className="graphiqlpanel-content">{body}</div>
      {footer ? <div className="graphiqlpanel-footer-bar">{footer}</div> : null}
    </div>
  )
}

export default GraphiQLPanel
